//Menu Button

.menu-btn {
    position: absolute;
    z-index: 3;
    right: 35px;
    top: 35px;
    cursor: pointer;
    @include easeOut;

    .btn-line {
        width: 28px;
        height: 3px;
        margin: 0 0 5px 0;
        background: #ffffff;
        @include easeOut;
    }

    //Rotate IntoX
    &.close {
        transform: rotate(180deg);

        .btn-line {
            //Line 1 - Rotate
            &:nth-child(1) {
                background: red;
                transform: rotate(45deg) translate(5px, 5px);
            }
            //Line 2 - Hide
            &:nth-child(2) {
                opacity: 0;
            }

            //Line 3 - Rotate
            &:nth-child(3) {
                background: red;
                transform: rotate(-45deg) translate(7px, -6px);
            }
        }
    }

}

